#page-contact .content div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  margin: 0 auto;
  height: 300px;
  width: 50%;
  min-width: 475px;
  background-color: black;
  border: 2px solid rgba(255, 255, 255, 0.6);
  border-radius: 15px 100px;
}

#page-contact .content div form * {
  margin-bottom: 10px;
  padding: 10px;
}

#contactme {
  background-color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.6);
  border-radius: 15px 30px;
  transition: all 0.5s ease;
}

#contactme:hover {
  cursor: pointer;
  background-color: black;
  color: #ffffff;
  transition: all 0.5s ease;
}

body {
  font-size: 16px;
  background: url("https://upload.wikimedia.org/wikipedia/commons/a/a5/Vengedalen_over_Vengedalsvatnet%2C_M%C3%B8re_og_Romsdal%2C_Norway_in_2013_June_-_2.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #ffffff;
}

#wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  height: 100px;
}

#wrapper-top {
  height: 850px;
  min-height: 902px;
  width: 95%;
  min-width: 730px;
}

#header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 280px;
  width: 100%;
  font-family: "'Archivo Black', sans-serif";
}

#header #jumbo {
  font-size: 3em;
  text-align: center;
  color: rgba(0, 0, 0, 0.4);
  -webkit-text-stroke: 1px #ffffff;
  text-stroke: 1px #ffffff;
}

#header #jumbo p {
  margin-top: -90px;
}

#header #header-navigation {
  margin-top: -30px;
  margin-bottom: 30px;
  background: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(255, 255, 255, 0.6);
  border-radius: 15px 50px;
}

#header #header-navigation button {
  padding: 20px;
  font-family: "'Archivo Black', sans-serif";
  font-size: 1.5em;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.6);
  color: rgba(255, 255, 255, 0);
  -webkit-text-stroke: 1px #ffffff;
  text-stroke: 1px #ffffff;
  background: none;
  border: 2px solid transparent;
  border-radius: 15px 50px;
  outline: none;
  transition: all 0.5s ease;
}

#header #header-navigation button:hover {
  cursor: pointer;
  text-shadow: none;
  color: black;
  -webkit-text-stroke: 1px black;
  text-stroke: 1px black;
  background: #ffffff;
  border: 2px solid rgba(0, 0, 0, 0.6);
  transition: all 1s ease;
}

#footer {
  margin-top: 50px;
  height: 100%;
}

#footer a {
  text-decoration: none;
  -webkit-text-stroke: 1px #ffffff;
  text-stroke: 1px #ffffff;
}

.active {
  text-shadow: none;
  color: black;
  background: #ffffff;
}

.page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  margin: 0 auto;
  height: 500px;
  width: 95%;
  font-family: "'Amaranth', sans-serif";
  background: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(255, 255, 255, 0.6);
  border-radius: 15px 100px;
  overflow: hidden;
}

.page .content {
  width: 80%;
  font-size: 1.2em;
  text-align: center;
}

.page .content .card {
  display: inline-block;
  height: 250px;
  width: 70%;
  background-size: cover !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
  border: 2px solid rgba(255, 255, 255, 0.6);
  transition: all 1s ease;
}

.page .content .card:hover {
  position: relative;
  padding-bottom: 200px;
  height: 500px;
  width: 100%;
  transition: all 1s ease;
}